@use '../abstracts/mixins/mixins' as *;
@use '../palette/palette' as *;
@use '../abstracts/variables' as variables;

.slider {
  display: flex;

  &__wrapper-navigation {
    display: flex;
  }

  &__button-navigation {
    @include slider-button-change-image;
    top: 25px;
    left: 0;
    cursor: pointer;

    &_prev {
      margin-right: 25px;
    }

    &_prev,
    &_next {
      &::after {
        background-image: url('../../images/components/Slider/slider-button-active.svg');
      }
      background-color: rgba($green-secondary-500, 0.25);
    }

    &_prev:not(&_disabled) {
      transform: rotate(180deg); // ÐµÑÐ»Ð¸ ÐµÑÑ‚ÑŒ _prev Ð¸ Ð½ÐµÑ‚Ñƒ _disabled
    }

    &_next:where(&_disabled) {
      transform: rotate(180deg); // ÐµÑÐ»Ð¸ ÐµÑÑ‚ÑŒ Ð¸ _next Ð¸ _disabled
    }

    &_disabled {
      background-color: rgba($white, 0.6);
      cursor: auto;

      &::after {
        background-image: url('../../images/components/Slider/slider-button-disabled.svg');
      }
    }
  }
}

@media screen and (min-width: 375px) {
  .slider {
    &_in-portfolio {
      flex-direction: column-reverse;
      align-items: center;
      align-self: self-start;
      margin: 0 auto;
    }

    &_in-customers {
      flex-direction: column-reverse;
      align-items: center;
      width: 100%;
      overflow: hidden;
    }
  }
}

@media screen and (min-width: 768px) {
  .slider {
    &_in-portfolio {
      position: relative;
      width: 100%;
      padding-left: variables.$min-pl-section;
      flex-direction: column;
    }

    &__wrapper-navigation {
      &_in-portfolio {
        align-self: self-start;
      }
    }
  }
}

@media screen and (min-width: 1024px) {
  .slider {
    &__button-navigation {
      &_in-customers {
        width: 38px;
        height: 38px;

        &::after {
          width: 28px;
          height: 28px;
        }
      }
    }
  }
}

